                <label className="text-sm font-medium">‡∏ä‡∏∑‡πà‡∏≠‡∏õ‡∏£‡∏∞‡πÄ‡∏†‡∏ó</label>
                <Input 
                  name="category" 
                  placeholder="‡πÄ‡∏ä‡πà‡∏ô Computer, Notebook, Printer" 
